'use client';

import React, { useState, useEffect, useMemo } from 'react';
import { useRouter } from 'next/navigation';
import { Card } from '@/components/ui/card';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/button/Button';
import Select, { SelectOption } from '@/components/ui/Select';
import { services } from '@/services/api';
import type { Empresa } from '@/services/api/empresa.service';
import type { User } from '@/services/api/users.service';
import type { CreateNotificacaoRequest } from '@/services/api/notificacao.service';
import toast from 'react-hot-toast';

export default function CriarNotificacaoPage() {
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [loadingEmpresas, setLoadingEmpresas] = useState(true);
  const [loadingUsuarios, setLoadingUsuarios] = useState(true);
  const [empresas, setEmpresas] = useState<Empresa[]>([]);
  const [usuarios, setUsuarios] = useState<User[]>([]);
  
  const [formData, setFormData] = useState<CreateNotificacaoRequest & { empresaId?: number; userId?: number }>({
    titulo: '',
    mensagem: '',
    tipo: 'MANUAL',
    empresaId: undefined,
    userId: undefined,
    actionLink: '',
    actionLabel: ''
  });

  // Carregar empresas
  useEffect(() => {
    const loadEmpresas = async () => {
      try {
        setLoadingEmpresas(true);
        const response = await services.empresa.list({ perPage: 1000 });
        setEmpresas(response.data || []);
      } catch (error) {
        console.error('Erro ao carregar empresas:', error);
        toast.error('Erro ao carregar empresas');
      } finally {
        setLoadingEmpresas(false);
      }
    };

    loadEmpresas();
  }, []);

  // Carregar usuários
  useEffect(() => {
    const loadUsuarios = async () => {
      try {
        setLoadingUsuarios(true);
        const response = await services.users.list({ limit: 1000 });
        setUsuarios(response.data || []);
      } catch (error) {
        console.error('Erro ao carregar usuários:', error);
        toast.error('Erro ao carregar usuários');
      } finally {
        setLoadingUsuarios(false);
      }
    };

    loadUsuarios();
  }, []);

  // Opções de empresas para o Select
  const empresaOptions: SelectOption[] = useMemo(() => {
    return empresas.map(empresa => ({
      value: String(empresa.id),
      label: empresa.nome
    }));
  }, [empresas]);

  // Opções de usuários para o Select (filtrados por empresa se empresa selecionada)
  const usuarioOptions: SelectOption[] = useMemo(() => {
    let usuariosFiltrados = usuarios;

    // Se uma empresa foi selecionada, filtrar usuários dessa empresa
    if (formData.empresaId) {
      usuariosFiltrados = usuarios.filter(user => user.empresaId === formData.empresaId);
    }

    return usuariosFiltrados.map(usuario => ({
      value: String(usuario.id),
      label: `${usuario.nome} (${usuario.email}) - ${usuario.role}`
    }));
  }, [usuarios, formData.empresaId]);

  // Opções de tipo de notificação
  const tipoOptions: SelectOption[] = [
    { value: 'MANUAL', label: 'Manual' },
    { value: 'VENCIMENTO_EMPRESA', label: 'Vencimento de Empresa' },
    { value: 'PLANO_PAGO', label: 'Plano Pago' },
    { value: 'COBRANCA_GERADA', label: 'Cobrança Gerada' },
    { value: 'FATURA_PAGA', label: 'Fatura Paga' },
    { value: 'PARCELA_PAGA', label: 'Parcela Paga' }
  ];

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSelectChange = (name: string) => (selectedOption: any) => {
    setFormData(prev => {
      const newData = { ...prev };
      const option = selectedOption as SelectOption | null;
      
      if (name === 'empresaId') {
        newData.empresaId = option ? Number(option.value) : undefined;
        // Se empresa mudou, limpar usuário selecionado
        if (prev.empresaId !== newData.empresaId) {
          newData.userId = undefined;
        }
      } else if (name === 'userId') {
        newData.userId = option ? Number(option.value) : undefined;
      } else if (name === 'tipo') {
        newData.tipo = option?.value as CreateNotificacaoRequest['tipo'];
      }
      
      return newData;
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.titulo.trim()) {
      toast.error('Título é obrigatório');
      return;
    }

    if (!formData.mensagem.trim()) {
      toast.error('Mensagem é obrigatória');
      return;
    }

    // Se empresa selecionada mas não usuário, notificar todos da empresa
    // Se usuário selecionado, notificar apenas esse usuário
    // Se nenhum selecionado, não é permitido (precisa de empresa ou usuário)

    if (!formData.empresaId && !formData.userId) {
      toast.error('Selecione uma empresa ou um usuário');
      return;
    }

    try {
      setLoading(true);

      const dataToSend: CreateNotificacaoRequest = {
        titulo: formData.titulo.trim(),
        mensagem: formData.mensagem.trim(),
        tipo: formData.tipo,
        empresaId: formData.empresaId || undefined,
        userId: formData.userId || undefined,
        actionLink: formData.actionLink?.trim() || undefined,
        actionLabel: formData.actionLabel?.trim() || undefined
      };

      await services.notificacao.create(dataToSend);
      
      toast.success('Notificação criada com sucesso!');
      router.push('/dashboard/admin');
    } catch (error: any) {
      console.error('Erro ao criar notificação:', error);
      toast.error(error?.response?.data?.error || 'Erro ao criar notificação');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
          Criar Notificação
        </h1>
      </div>

      <Card className="p-6">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Título */}
          <div>
            <label htmlFor="titulo" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Título <span className="text-red-500">*</span>
            </label>
            <Input
              id="titulo"
              name="titulo"
              type="text"
              value={formData.titulo}
              onChange={handleInputChange}
              placeholder="Digite o título da notificação"
              required
              aria-label="Título da notificação"
            />
          </div>

          {/* Mensagem */}
          <div>
            <label htmlFor="mensagem" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Mensagem <span className="text-red-500">*</span>
            </label>
            <textarea
              id="mensagem"
              name="mensagem"
              value={formData.mensagem}
              onChange={handleInputChange}
              placeholder="Digite a mensagem da notificação"
              required
              rows={5}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-800 dark:border-gray-700 dark:text-white"
              aria-label="Mensagem da notificação"
            />
          </div>

          {/* Tipo */}
          <div>
            <label htmlFor="tipo" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Tipo de Notificação <span className="text-red-500">*</span>
            </label>
            <Select
              id="tipo"
              options={tipoOptions}
              value={tipoOptions.find(opt => opt.value === formData.tipo) || null}
              onChange={handleSelectChange('tipo')}
              placeholder="Selecione o tipo"
              isClearable={false}
              aria-label="Tipo de notificação"
            />
          </div>

          {/* Empresa */}
          <div>
            <label htmlFor="empresaId" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Empresa
              <span className="text-xs text-gray-500 ml-2">
                (Se selecionada, notificará todos os usuários ADMIN, GESTOR e VENDEDOR da empresa)
              </span>
            </label>
            <Select
              id="empresaId"
              options={empresaOptions}
              value={formData.empresaId ? empresaOptions.find(opt => opt.value === String(formData.empresaId)) || null : null}
              onChange={handleSelectChange('empresaId')}
              placeholder={loadingEmpresas ? "Carregando empresas..." : "Selecione uma empresa (opcional)"}
              isClearable
              isSearchable
              isLoading={loadingEmpresas}
              aria-label="Selecionar empresa"
            />
          </div>

          {/* Usuário */}
          <div>
            <label htmlFor="userId" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Usuário Específico
              <span className="text-xs text-gray-500 ml-2">
                (Se selecionado, notificará apenas este usuário. Se empresa também selecionada, filtra usuários da empresa)
              </span>
            </label>
            <Select
              id="userId"
              options={usuarioOptions}
              value={formData.userId ? usuarioOptions.find(opt => opt.value === String(formData.userId)) || null : null}
              onChange={handleSelectChange('userId')}
              placeholder={loadingUsuarios ? "Carregando usuários..." : "Selecione um usuário (opcional)"}
              isClearable
              isSearchable
              isLoading={loadingUsuarios}
              aria-label="Selecionar usuário"
            />
          </div>

          {/* Action Link */}
          <div>
            <label htmlFor="actionLink" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Link de Ação (opcional)
            </label>
            <Input
              id="actionLink"
              name="actionLink"
              type="url"
              value={formData.actionLink || ''}
              onChange={handleInputChange}
              placeholder="Ex: /dashboard/empresa/planos/atual"
              aria-label="Link de ação da notificação"
            />
            <p className="mt-1 text-xs text-gray-500">
              URL para onde o usuário será redirecionado ao clicar na notificação
            </p>
          </div>

          {/* Action Label */}
          <div>
            <label htmlFor="actionLabel" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Rótulo do Botão de Ação (opcional)
            </label>
            <Input
              id="actionLabel"
              name="actionLabel"
              type="text"
              value={formData.actionLabel || ''}
              onChange={handleInputChange}
              placeholder="Ex: Ver Plano Atual"
              aria-label="Rótulo do botão de ação"
            />
            <p className="mt-1 text-xs text-gray-500">
              Texto que aparecerá no botão de ação da notificação
            </p>
          </div>

          {/* Botões */}
          <div className="flex gap-4 pt-4">
            <Button
              type="submit"
              variant="primary"
              disabled={loading}
            >
              {loading ? 'Criando...' : 'Criar Notificação'}
            </Button>
            <Button
              type="button"
              variant="outline"
              onClick={() => router.back()}
              disabled={loading}
            >
              Cancelar
            </Button>
          </div>
        </form>
      </Card>
    </div>
  );
}

